<template lang="pug">
.background(:style="{backgroundColor}")
  .background-holder(ref="colHolder" :style="{transitionDuration}")
    .column(@click.stopPropagation=""  v-for="n in numColumns" :key="n" :style="{top: getTopColOffset(n), left: getLeftColOffset(n), animationDuration: getDurationCol(n), width: colWidth, height: colHeight, animationDelay: randomDelayFor(n)}")
      img(v-for="(src,i) in randomBgImagesForCols[n-1]" :key="bgIndex+'_'+i" :style="imgStyle(src)" :src="src")
      img(v-for="(src,i) in randomBgImagesForCols[n-1]" :key="bgIndex+'_'+(i+ randomBgImagesForCols[n-1].length)" :style="imgStyle(src)" :src="src")
  img-load-list(v-for="ider in bgSetIds" :key="ider" :uid="ider" :img-list="getCombined2DArray(bgImages[ider], ider)" @complete="onLoadListComplete" @single-loaded="onImgLoad")
</template>

<script>

  import ImgLoadList from './ImgLoadList.vue';
 //const imgSizeOf = require('image-size');

  const COL_WIDTH = 228;
  const TRANSITION_DURATION = .45;
  const MIN_DURATION = 90;
  
  
  export default {
    mounted () {
      this.$refs.colHolder.classList.add("fadeout");
      window.addEventListener("resize", this.onScreenResize.bind(this));
    },
    components: {
      ImgLoadList
    },
    data () {
      return {
        bgImgWidthCache: {},
        combined2DArrCache: {},
        loadCompleteMask: 0,
        randomTimeCache: [],  // for delays
        randomDurationCache: [],  // for durations
        randomImgsCache: [],
        transitionInProgress: false,
        transitionLoading: true,
        bgSetIds: [
          "kale", "vanilla", "banana", "strawberry"
        ],
        bgColors: {
          kale: "#e1d2cb",
          vanilla: "#dcece9",
          banana: "#f2e6b9",
          strawberry: "#f8e2dc"
        },
        bgImages: {
          kale: [ 
            [ require(`../assets/images/backgrounds/kale/choco_a.png`), 
              require(`../assets/images/backgrounds/kale/choco_b.png`), 
              require(`../assets/images/backgrounds/kale/choco_a.png`)
            ],
            [ require(`../assets/images/backgrounds/kale/kale_a.png`), 
              require(`../assets/images/backgrounds/kale/kale_b.png`)
            ]
          ],
          vanilla: [ 
            [ require(`../assets/images/backgrounds/vanilla/chia_a.png`), 
              require(`../assets/images/backgrounds/vanilla/chia_b.png`), 
              require(`../assets/images/backgrounds/vanilla/chia_c.png`)
            ],
            [ require(`../assets/images/backgrounds/vanilla/vanilla_a.png`), 
              require(`../assets/images/backgrounds/vanilla/vanilla_b.png`),
              require(`../assets/images/backgrounds/vanilla/vanilla_c.png`)
            ]
          ],
          banana: [ 
            [ require(`../assets/images/backgrounds/banana/banana_a.png`), 
              require(`../assets/images/backgrounds/banana/banana_b.png`), 
            ],
            [ require(`../assets/images/backgrounds/banana/date_a.png`), 
              require(`../assets/images/backgrounds/banana/date_b.png`), 
              require(`../assets/images/backgrounds/banana/date_c.png`)
            ]
          ],
          strawberry: [ 
            [ require(`../assets/images/backgrounds/strawberry/strawberry_a.png`), 
              require(`../assets/images/backgrounds/strawberry/strawberry_b.png`), 
              require(`../assets/images/backgrounds/strawberry/strawberry_c.png`)
            ],
            [ require(`../assets/images/backgrounds/strawberry/yoghurt.png`), 
            ]
          ]
        },
        screen: {
          width: window.innerWidth,
          height: window.innerHeight
        }
      }
    },
    watch: {
      bgIndex () {
        //this.randomImgsCache = [];
        if (this.transitionInProgress) {
          return;
        }
        this.transitionInProgress = true;
        
        this.$refs.colHolder.classList.add("fadeout");
        setTimeout(()=>{
          this.randomImgsCache = [];
          this.randomDurationCache = [];
          this.randomTimeCache = [];
          this.$nextTick(()=>{   
            if ( (this.loadCompleteMask & (1<<this.bgIndex))!=0 ) {
              this.fadeInContents();
            } else {
              this.transitionLoading = true;
             // console.log("STILL LOADING");
            }
          })
        }, TRANSITION_DURATION * 1000);
        //this.$refs.colHolder.classlist.add("fadeout");
      }
    },
    methods: {
      fadeInContents () {
        this.transitionLoading = false;
        this.transitionInProgress = false;
        this.$refs.colHolder.classList.remove("fadeout");
      },
      onImgLoad (src, image) {
        if (!this.bgImgWidthCache[src]) {
          this.$set(this.bgImgWidthCache, src, (image.width*0.5)+"px");
        }
      },
      onLoadListComplete (uid) {
        // console.log('cc:'+this.bgSetIds.indexOf(uid));
        var index = this.bgSetIds.indexOf(uid);
        if (index < 0) return;
        this.loadCompleteMask |= (1 << index);
        console.log("load complete:"+index);
        if (this.transitionLoading && this.bgIndex === index) {
          this.fadeInContents();
        }
        this.$emit("loadListComplete", index);
      },
      imgStyle (src) {
        return {visibility: this.bgImgWidthCache[src] ? 'visible' : 'hidden', width:this.bgImgWidthCache[src]}; //, 
      },
      getCombined2DArray (arr, cacheId) {
        if (cacheId && this.combined2DArrCache[cacheId]) {
          return this.combined2DArrCache[cacheId];
        }
        var cArr = arr[0].concat();
        var i;
        for (i=1; i<arr.length;i++) {
          cArr = cArr.concat(arr[i]);
        }
        if (cacheId) this.combined2DArrCache[cacheId] = cArr;
        return cArr;
      },
      randomDelayFor (n) {
        var randomTimeCache = this.randomTimeCache;
        if (n >= randomTimeCache) randomTimeCache.length = n;
        if (randomTimeCache[n-1]) {
          return randomTimeCache[n-1];
        } else {
          return (randomTimeCache[n-1]= (-Math.random()*MIN_DURATION)+"s" );
        }
      },
      onScreenResize () {
        this.screen.width = window.innerWidth;
        this.screen.height = window.innerHeight;
      },
      getLeftColOffset (n) {
        return (n-1)* (this.rotScreenWidth/this.numColumns) + "px";
      },
      getTopColOffset (n) {
        var colWidthPx = this.colWidthAmt;
        var norm = this.norm;
        var displaceHoriz = colWidthPx*n;
        var result = displaceHoriz / norm.x * norm.y;
        return -result + "px"; //-n.y*(colWidthPx*n) + "px";
      },
      getDurationCol (n) {
         var randomDurationCache = this.randomDurationCache;
         if (randomDurationCache[n]) return randomDurationCache[n];
        return (randomDurationCache[n]=MIN_DURATION+(Math.random()*20)+'s');
      }
    },
    computed: {
      transitionDuration () {
        return TRANSITION_DURATION + "s";
      },
      backgroundColor () {
        var ider = this.bgSetIds[this.bgIndex];
        return this.bgColors[ider];
      },
      randomBgImagesForCols() {
        var bgs = this.bgSetIds[this.bgIndex];
        bgs = this.bgImages[bgs];
        var bit;
        var candidates;
        var i;

        var arr = [];
        var newArr;
        bit = 0;
        var c;

        for (c = 0; c < this.numColumns; c++) {
          if (this.randomImgsCache[c]) {
            arr.push(this.randomImgsCache[c]);
          } else {
            newArr = [];
            for (i=0; i< 16; i++) {
              candidates = bgs[bit];
              newArr.push(candidates[Math.floor(candidates.length*Math.random())]);
              bit ^= 1;
            }
            // eslint-disable-next-line 
            this.randomImgsCache[c] = newArr;
            arr.push(newArr);
          }
        }
        return arr;
      },
      norm () {
        var dx = 1;
        var dy = 1;
        var d = Math.sqrt( dx * dx + dy * dy);
        dx /=d;
        dy /=d;
        return {x:dx, y:dy}
      },
      rotScreenWidth () {
        var n = this.norm;
        return n.x + this.screen.width + n.y*this.screen.height;
      },
      colWidthAmt () {
        return this.rotScreenWidth/this.numColumns;
      },
      colWidth () {
        return this.colWidthAmt + "px";
      },
      colHeight () {
        return "auto";
       // var screenWidth = this.rotScreenWidth;
        //return Math.sqrt(screenWidth*screenWidth + screenWidth*screenWidth) + "px";
        /*
        var n = this.norm;
        var d = n.x * this.screen.width + n.y * this.screen.height;
        var colWidthPx = this.colWidthAmt;
        d += colWidthPx / n.x * n.y;
        return d + "px";
        */
        //return "100vh";
      },
      numColumns () {
        return Math.max(3, Math.ceil(this.rotScreenWidth / COL_WIDTH) );
      },
    },
    props: {
      bgIndex: {
        type: Number,
        default: 0
      },
    },
  }
</script>

<style lang="scss" scoped>
  .background {
      width:100%;
      height:100%;
      overflow:hidden;
      position:absolute;
      top:0;
      left:0;
      //background: #FCE5DF;
      transition:background-color .65s;
      z-index: -1;
  }
  .background-holder {
    transform-origin:top left;
    transform:translate3d(0,0,0) rotate(45deg);
    transform-style: preserve-3d;
    backface-visibility: hidden;
    transition:opacity .3s;
    &.fadeout {
      opacity:0;
    }
  }
  .column {
    background-repeat:repeat-y;
    position:absolute;
    //transform:translateY(-25%);
    animation-name: scroll;
    animation-duration:20s;
    animation-iteration-count: infinite;
    animation-timing-function:linear;
     transform-style: preserve-3d;
     backface-visibility: hidden;
    //outline:red solid 1px;
    /*
    &:nth-child(odd) {
      animation-name:scroll-reverse;
    }
    */
    :global(img) {
      display:block;
      //width:100%;
    
      height:auto;
      max-width:435px/2;
      margin:0 auto 55px;

    }

    @keyframes scroll {
      from {transform: translate3d(0,0,0);}
      to {transform: translate3d(0,-50%,0)}
    }
     @keyframes scroll-reverse {
      to {transform: translate3d(0,0,0);}
      from {transform: translate3d(0,-50%,0)}
    }
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>