<template lang="pug">
  div(style="display:none" :data-meta="'imgloadlist-'+(uid ? uid : '')")
    img(v-for="(src,i) in imgList" :src="src" :key="invalidateCount + '_' + i" @load="imgLoadedHandler(src, $event.target)")
</template>


<script>
	export default {
    data() {
      return {
       loadedCount: 0,
       invalidateCount: 0
      }
    },
		props: {
			uid: {
				type: String
			},
			imgList: {
				type: Array
      },
    },
    watch: {
      imgList(newValue) {
        this.loadedCount = 0;
        if (!newValue) return;
        this.invalidateCount++;
      }
    },
    methods: {
      imgLoadedHandler(src) {
        this.loadedCount++;
        this.$emit("single-loaded", src, event.target, this.loadedCount, this.uid);
        if (this.loadedCount >= this.imgList.length) {
          this.$emit("complete", this.uid);
        }
      }
    },
	}
</script>

